/**
 * Hook to fetch a single city by ID (returns complete details with nested state/country)
 * @param id - City ID
 * @param options - Hook options
 */
export function useCity(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: CityMaster) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const queryFn = useCallback(async () => {
    const response = await pricingApiClient.get<ApiResponse<CityMaster>>(
      `/city/${id}`
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as CityMaster;
  }, [id]);

  return useQuery<CityMaster>({
    queryKey: ['city', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

