/**
 * Promotions Management API Hooks
 * React hooks for managing promotions data
 */

import { useCallback } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { pricingApiClient } from './pricingApiClient';
import {
  Promotion,
  PromotionDTO,
  PromotionListQueryParams,
  PagedData,
  PromotionForm
} from './promotionsTypes';
import { ApiResponse } from './types';
import { CityMaster, VehicleCategory } from './pricingTypes';

/**
 * Hook to fetch a single promotion by ID
 * @param id - The promotion ID
 * @param options - Hook options
 */
export function usePromotion(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: Promotion) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const response = await pricingApiClient.get<ApiResponse<Promotion>>(`/promotions/${id}`);

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as Promotion;
  }, [id]);

  return useQuery<Promotion>({
    queryKey: ['promotion', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to fetch paginated list of promotions
 * @param params - Query parameters (page, size, sort, search)
 * @param options - Hook options
 */
export function usePromotions(
  params: PromotionListQueryParams = {},
  options: {
    enabled?: boolean;
    onSuccess?: (data: PagedData<Promotion>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};

    if (params.page !== undefined) {
      queryParams.page = params.page;
    }
    if (params.size !== undefined) {
      queryParams.size = params.size;
    }
    if (params.search) {
      queryParams.search = params.search;
    }
    if (params.vehicleCategoryId) {
      queryParams.vehicleCategoryId = params.vehicleCategoryId;
    }
    if (params.cityId) {
      queryParams.cityId = params.cityId;
    }
    if (params.isActive !== undefined) {
      queryParams.isActive = params.isActive;
    }
    if (params.discountType) {
      queryParams.discountType = params.discountType;
    }
    if (params.applicableUserType) {
      queryParams.applicableUserType = params.applicableUserType;
    }
    if (params.validFrom) {
      queryParams.validFrom = params.validFrom;
    }
    if (params.validTo) {
      queryParams.validTo = params.validTo;
    }
    if (params.sort && params.sort.length > 0) {
      queryParams.sort = params.sort;
    }

    const response = await pricingApiClient.get<ApiResponse<PagedData<Promotion>>>(
      '/promotions/list',
      queryParams
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as PagedData<Promotion>;
  }, [params.page, params.size, params.sort, params.search, params.vehicleCategoryId, params.cityId, params.isActive, params.discountType, params.applicableUserType, params.validFrom, params.validTo]);

  return useQuery<PagedData<Promotion>>({
    queryKey: ['promotions', params],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

