/**
 * Hook to create a new promotion
 * @param options - Hook options
 */
export function useCreatePromotion(
  options: {
    onSuccess?: (data: ApiResponse<Promotion>, variables: PromotionDTO) => void;
    onError?: (error: Error, variables: PromotionDTO) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<Promotion>, Error, PromotionDTO>({
    mutationFn: async (promotionData: PromotionDTO) => {
      const response = await pricingApiClient.post<ApiResponse<Promotion>>(
        '/promotions',
        promotionData
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all promotion queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['promotions'] });
      queryClient.invalidateQueries({ queryKey: ['all-promotions'] });
      queryClient.invalidateQueries({ queryKey: ['active-promotions'] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

/**
 * Hook to update an existing promotion
 * @param options - Hook options
 */
export function useUpdatePromotion(
  options: {
    onSuccess?: (data: ApiResponse<Promotion>, variables: { id: number; data: PromotionDTO }) => void;
    onError?: (error: Error, variables: { id: number; data: PromotionDTO }) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<Promotion>, Error, { id: number; data: PromotionDTO }>({
    mutationFn: async ({ id, data }) => {
      const response = await pricingApiClient.put<ApiResponse<Promotion>>(
        `/promotions/${id}`,
        data
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all promotion queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['promotions'] });
      queryClient.invalidateQueries({ queryKey: ['all-promotions'] });
      queryClient.invalidateQueries({ queryKey: ['active-promotions'] });
      queryClient.invalidateQueries({ queryKey: ['promotion', variables.id] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

