/**
 * Utility function to determine promotion status based on dates
 */
export function getPromotionStatus(promotion: Promotion): 'active' | 'scheduled' | 'expired' | 'inactive' {
  if (!promotion.isActive) {
    return 'inactive';
  }

  const now = new Date();
  const validFrom = new Date(promotion.validFrom);
  const validTo = new Date(promotion.validTo);

  if (now < validFrom) {
    return 'scheduled';
  } else if (now > validTo) {
    return 'expired';
  } else {
    return 'active';
  }
}

/**
 * Utility function to check if a promotion is currently valid
 */
export function isPromotionCurrentlyValid(promotion: Promotion): boolean {
  if (!promotion.isActive) {
    return false;
  }

  const now = new Date();
  const validFrom = new Date(promotion.validFrom);
  const validTo = new Date(promotion.validTo);

  return now >= validFrom && now <= validTo;
}

