/**
 * Driver Management API Hooks
 * React hooks for managing driver data
 */

import { useQuery, useMutation } from '@tanstack/react-query';
import { useCallback } from 'react';
import * as React from 'react';
import { adminApiClient } from './adminApiClient';
import { DriverDTO } from './adminTypes';
import { ApiResponse, Pageable, PagedData } from './types';

/**
 * Hook to fetch a single driver by ID
 * @param id - Driver ID
 * @param options - Hook options
 */
export function useDriver(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: DriverDTO) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const response = await adminApiClient.get<ApiResponse<DriverDTO>>(`/drivers/${id}`);

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response as any;
  }, [id]);

  const query = useQuery<DriverDTO>({
    queryKey: ['driver', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
  });

  // Handle callbacks using useEffect pattern (React Query v5)
  React.useEffect(() => {
    if (query.isSuccess && query.data && options.onSuccess) {
      options.onSuccess(query.data);
    }
  }, [query.isSuccess, query.data, options.onSuccess]);

  React.useEffect(() => {
    if (query.isError && query.error && options.onError) {
      options.onError(query.error);
    }
  }, [query.isError, query.error, options.onError]);

  return {
    ...query,
    data: query.data,
    loading: query.isLoading || query.isFetching,
    error: query.error,
  };
}

/**
 * Hook to fetch paginated list of drivers
 * @param params - Query parameters (page, size, sort, search, status, kycStatus, isActive)
 * @param options - Hook options
 */
export function useDrivers(
  params: {
    page?: number;
    size?: number;
    sort?: string[];
    search?: string;
    status?: string;
    kycStatus?: string;
    isActive?: boolean;
  } = {},
  options: {
    enabled?: boolean;
    onSuccess?: (data: PagedData<DriverDTO>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};

    if (params.page !== undefined) {
      queryParams.page = params.page;
    }
    if (params.size !== undefined) {
      queryParams.size = params.size;
    }
    if (params.sort && params.sort.length > 0) {
      queryParams.sort = params.sort;
    }
    if (params.search) {
      queryParams.search = params.search;
    }
    if (params.status) {
      queryParams.status = params.status;
    }
    if (params.kycStatus) {
      queryParams.kycStatus = params.kycStatus;
    }
    if (params.isActive !== undefined) {
      queryParams.isActive = params.isActive;
    }

    const response = await adminApiClient.get<ApiResponse<PagedData<DriverDTO>>>(
      '/drivers/list',
      queryParams
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    // If data is directly in response (array)
    if (Array.isArray(response)) {
      return {
        content: response,
        page: {
          size: response.length,
          number: 0,
          totalElements: response.length,
          totalPages: 1,
        },
        first: true,
        last: true,
        empty: response.length === 0,
      };
    }

    return response as any;
  }, [params.page, params.size, params.sort, params.search, params.status, params.kycStatus, params.isActive]);

  return useQuery<PagedData<DriverDTO>>({
    queryKey: ['drivers', params],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to create a new driver
 * @param options - Hook options
 */
export function useCreateDriver(
  options: {
    onSuccess?: (data: ApiResponse<DriverDTO>, variables: DriverDTO) => void;
    onError?: (error: Error, variables: DriverDTO) => void;
  } = {}
) {
  return useMutation<ApiResponse<DriverDTO>, Error, DriverDTO>({
    mutationFn: async (driverData: DriverDTO) => {
      const response = await adminApiClient.post<ApiResponse<DriverDTO>>('/drivers', driverData);
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to update an existing driver
 * @param options - Hook options
 */
export function useUpdateDriver(
  options: {
    onSuccess?: (
      data: ApiResponse<DriverDTO>,
      variables: { id: number; data: DriverDTO }
    ) => void;
    onError?: (error: Error, variables: { id: number; data: DriverDTO }) => void;
  } = {}
) {
  return useMutation<ApiResponse<DriverDTO>, Error, { id: number; data: DriverDTO }>({
    mutationFn: async ({ id, data }: { id: number; data: DriverDTO }) => {
      const response = await adminApiClient.put<ApiResponse<DriverDTO>>(`/drivers/${id}`, data);
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to delete a driver
 * @param options - Hook options
 */
export function useDeleteDriver(
  options: {
    onSuccess?: (data: ApiResponse<void>, variables: DeleteDriverVariables) => void;
    onError?: (error: Error, variables: DeleteDriverVariables) => void;
  } = {}
) {
  return useMutation<ApiResponse<void>, Error, DeleteDriverVariables>({
    mutationFn: async (variables: DeleteDriverVariables) => {
      // First delete the driver
      const response = await adminApiClient.delete<ApiResponse<void>>(`/drivers/${variables.driverId}`);

      // Then find and delete the corresponding user if email is provided
      if (variables.email) {
        try {
          const userResponse = await adminApiClient.get<any>(`/api/admin/user-by-email?email=${encodeURIComponent(variables.email)}`);
          if (userResponse.data?.id) {
            await adminApiClient.delete<ApiResponse<void>>(`/api/admin/${userResponse.data.id}`);
          }
        } catch (userError) {
          // Log the error but don't fail the entire operation
          console.warn('Failed to delete corresponding user:', userError);
        }
      }

      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Variables for delete driver operation
 */
export interface DeleteDriverVariables {
  driverId: number;
  email?: string;
  phoneNumber?: string;
}

/**
 * Hook to fetch drivers by KYC status
 * @param kycStatus - KYC status filter
 * @param options - Hook options
 */
export function useDriversByKycStatus(
  kycStatus?: string,
  options: {
    enabled?: boolean;
    onSuccess?: (data: DriverDTO[]) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};
    if (kycStatus) {
      queryParams.kycStatus = kycStatus;
    }

    const response = await adminApiClient.get<ApiResponse<DriverDTO[]>>(
      '/drivers/drivers-list-by-kyc-status',
      queryParams
    );

    if (response.data) {
      return response.data;
    }

    return response as any;
  }, [kycStatus]);

  return useQuery<DriverDTO[]>({
    queryKey: ['drivers-by-kyc-status', kycStatus],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

