/**
 * Hook to upload profile photo
 * @param options - Hook options
 */
export function useUploadProfilePhoto(
  options: {
    onSuccess?: (data: ApiResponse<DriverDTO>, variables: { driverId: number; file: File }) => void;
    onError?: (error: Error, variables: { driverId: number; file: File }) => void;
  } = {}
) {
  return useMutation<ApiResponse<DriverDTO>, Error, { driverId: number; file: File }>({
    mutationFn: async ({ driverId, file }) => {
      const formData = new FormData();
      formData.append('profilePhotoUrl', file);

      const response = await adminApiClient.put<ApiResponse<DriverDTO>>(
        `/drivers/upload-profile-photo/${driverId}`,
        formData
      );
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to upload KYC documents
 * @param options - Hook options
 */
export function useUploadKycDocuments(
  options: {
    onSuccess?: (
      data: ApiResponse<DriverDTO>,
      variables: {
        driverId: number;
        govtIdType: string;
        govtIdNumber: string;
        files: {
          govtIdFrontUrl: File;
          govtIdBackUrl: File;
          drivingLicenseFrontUrl: File;
          drivingLicenseBackUrl: File;
          selfieWithIdUrl: File;
        };
      }
    ) => void;
    onError?: (
      error: Error,
      variables: {
        driverId: number;
        govtIdType: string;
        govtIdNumber: string;
        files: {
          govtIdFrontUrl: File;
          govtIdBackUrl: File;
          drivingLicenseFrontUrl: File;
          drivingLicenseBackUrl: File;
          selfieWithIdUrl: File;
        };
      }
    ) => void;
  } = {}
) {
  return useMutation<
    ApiResponse<DriverDTO>,
    Error,
    {
      driverId: number;
      govtIdType: string;
      govtIdNumber: string;
      files: {
        govtIdFrontUrl: File;
        govtIdBackUrl: File;
        drivingLicenseFrontUrl: File;
        drivingLicenseBackUrl: File;
        selfieWithIdUrl: File;
      };
    }
  >({
    mutationFn: async ({ driverId, govtIdType, govtIdNumber, files }) => {
      const formData = new FormData();
      formData.append('govtIdFrontUrl', files.govtIdFrontUrl);
      formData.append('govtIdBackUrl', files.govtIdBackUrl);
      formData.append('drivingLicenseFrontUrl', files.drivingLicenseFrontUrl);
      formData.append('drivingLicenseBackUrl', files.drivingLicenseBackUrl);
      formData.append('selfieWithIdUrl', files.selfieWithIdUrl);

      const queryParams = {
        govtIdType,
        govtIdNumber,
        driverId,
      };

      const response = await adminApiClient.post<ApiResponse<DriverDTO>>(
        '/drivers/upload-kyc-documents',
        formData,
        { params: queryParams }
      );
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

