/**
 * Dispute Management API Hooks
 * React hooks for managing dispute data
 */

import { useQuery, useMutation } from '@tanstack/react-query';
import { useCallback } from 'react';
import * as React from 'react';
import { riderApiClient } from './riderApiClient';
import {
  DisputeDTO,
  DisputeListParams,
  DisputeStats,
  ResolveDisputeRequest,
  RejectDisputeRequest,
} from './disputeTypes';
import { ApiResponse, PagedData } from './types';

/**
 * Hook to fetch a single dispute by ID
 * @param id - Dispute ID
 * @param options - Hook options
 */
export function useDispute(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: DisputeDTO) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const queryFn = useCallback(async () => {
    const response = await riderApiClient.get<ApiResponse<DisputeDTO>>(`/dispute/${id}`);

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response as any;
  }, [id]);

  const query = useQuery<DisputeDTO>({
    queryKey: ['dispute', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
  });

  // Handle callbacks using useEffect pattern (React Query v5)
  React.useEffect(() => {
    if (query.isSuccess && query.data && options.onSuccess) {
      options.onSuccess(query.data);
    }
  }, [query.isSuccess, query.data, options.onSuccess]);

  React.useEffect(() => {
    if (query.isError && query.error && options.onError) {
      options.onError(query.error);
    }
  }, [query.isError, query.error, options.onError]);

  return {
    ...query,
    data: query.data,
    loading: query.isLoading || query.isFetching,
    error: query.error,
  };
}

/**
 * Hook to fetch paginated list of disputes
 * @param params - Query parameters (page, size, sort, status, disputeType)
 * @param options - Hook options
 */
export function useDisputes(
  params: DisputeListParams = {},
  options: {
    enabled?: boolean;
    onSuccess?: (data: PagedData<DisputeDTO>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};

    // Build pageable object
    if (params.page !== undefined) {
      queryParams.page = params.page;
    }
    if (params.size !== undefined) {
      queryParams.size = params.size;
    }
    if (params.sort && params.sort.length > 0) {
      queryParams.sort = params.sort;
    }
    if (params.status) {
      queryParams.status = params.status;
    }
    if (params.disputeType) {
      queryParams.disputeType = params.disputeType;
    }

    const response = await riderApiClient.get<ApiResponse<PagedData<DisputeDTO>>>(
      '/dispute/list',
      queryParams
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    // If data is directly in response (array)
    if (Array.isArray(response)) {
      return {
        content: response,
        page: {
          size: response.length,
          number: 0,
          totalElements: response.length,
          totalPages: 1,
        },
        first: true,
        last: true,
        empty: response.length === 0,
      };
    }

    return response as any;
  }, [params.page, params.size, params.sort, params.status, params.disputeType]);

  const query = useQuery<PagedData<DisputeDTO>>({
    queryKey: ['disputes', params],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });

  return {
    ...query,
    data: query.data,
    loading: query.isLoading || query.isFetching,
    error: query.error,
  };
}

/**
 * Hook to fetch disputes by user (rider/driver)
 * @param userId - User ID
 * @param userType - User type (DRIVER, RIDER, OTHER, ADMIN)
 * @param status - Optional status filter
 * @param options - Hook options
 */
export function useDisputesByUser(
  userId: number,
  userType: 'DRIVER' | 'RIDER' | 'OTHER' | 'ADMIN',
  status?: 'OPEN' | 'IN_PROGRESS' | 'RESOLVED' | 'REJECTED',
  options: {
    enabled?: boolean;
    onSuccess?: (data: DisputeDTO[]) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};
    if (status) {
      queryParams.status = status;
    }

    const response = await riderApiClient.get<ApiResponse<DisputeDTO[]>>(
      `/dispute/user/${userType}/${userId}`,
      queryParams
    );

    if (response.data) {
      return response.data;
    }

    return response as any;
  }, [userId, userType, status]);

  return useQuery<DisputeDTO[]>({
    queryKey: ['disputes-by-user', userId, userType, status],
    queryFn,
    enabled: options.enabled !== false && !!userId,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

