/**
 * Hook to create a new dispute
 * @param options - Hook options
 */
export function useCreateDispute(
  options: {
    onSuccess?: (data: ApiResponse<DisputeDTO>, variables: DisputeDTO) => void;
    onError?: (error: Error, variables: DisputeDTO) => void;
  } = {}
) {
  return useMutation<ApiResponse<DisputeDTO>, Error, DisputeDTO>({
    mutationFn: async (disputeData: DisputeDTO) => {
      const response = await riderApiClient.post<ApiResponse<DisputeDTO>>('/dispute', disputeData);
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to update an existing dispute
 * @param options - Hook options
 */
export function useUpdateDispute(
  options: {
    onSuccess?: (
      data: ApiResponse<DisputeDTO>,
      variables: { id: number; data: DisputeDTO }
    ) => void;
    onError?: (error: Error, variables: { id: number; data: DisputeDTO }) => void;
  } = {}
) {
  return useMutation<ApiResponse<DisputeDTO>, Error, { id: number; data: DisputeDTO }>({
    mutationFn: async ({ id, data }: { id: number; data: DisputeDTO }) => {
      const response = await riderApiClient.put<ApiResponse<DisputeDTO>>(`/dispute/${id}`, data);
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to delete a dispute
 * @param options - Hook options
 */
export function useDeleteDispute(
  options: {
    onSuccess?: (data: ApiResponse<void>, variables: number) => void;
    onError?: (error: Error, variables: number) => void;
  } = {}
) {
  return useMutation<ApiResponse<void>, Error, number>({
    mutationFn: async (id: number) => {
      const response = await riderApiClient.delete<ApiResponse<void>>(`/dispute/${id}`);
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to resolve a dispute
 * @param options - Hook options
 */
export function useResolveDispute(
  options: {
    onSuccess?: (data: ApiResponse<DisputeDTO>, variables: ResolveDisputeRequest) => void;
    onError?: (error: Error, variables: ResolveDisputeRequest) => void;
  } = {}
) {
  return useMutation<ApiResponse<DisputeDTO>, Error, ResolveDisputeRequest>({
    mutationFn: async ({ id, resolution }: ResolveDisputeRequest) => {
      if (!id) {
        throw new Error('Dispute ID is required');
      }
      const response = await riderApiClient.put<ApiResponse<DisputeDTO>>(
        `/dispute/${id}/resolve`,
        undefined,
        { params: { resolution } }
      );
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to reject a dispute
 * @param options - Hook options
 */
export function useRejectDispute(
  options: {
    onSuccess?: (data: ApiResponse<DisputeDTO>, variables: RejectDisputeRequest) => void;
    onError?: (error: Error, variables: RejectDisputeRequest) => void;
  } = {}
) {
  return useMutation<ApiResponse<DisputeDTO>, Error, RejectDisputeRequest>({
    mutationFn: async ({ id, resolution }: RejectDisputeRequest) => {
      if (!id) {
        throw new Error('Dispute ID is required');
      }
      const response = await riderApiClient.put<ApiResponse<DisputeDTO>>(
        `/dispute/${id}/reject`,
        undefined,
        { params: { resolution } }
      );
      return response;
    },
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

