/**
 * Authentication Hooks
 * React hooks for managing authentication and login
 */

import { useState, useCallback, useEffect } from 'react';
import { useMutation, useQuery } from './useBaseHooks';
import { adminApiClient } from './adminApiClient';
import {
  LoginRequest,
  LoginResponse,
  ForgotPasswordRequest,
  ResetPasswordRequest,
  ChangePasswordRequest,
  AdminUserResponse,
  ApiResponse
} from './adminTypes';

/**
 * Hook to handle admin login
 * @param options - Hook options
 */
export function useLogin(
  options: {
    onSuccess?: (data: LoginResponse, credentials: LoginRequest) => void;
    onError?: (error: Error, credentials: LoginRequest) => void;
  } = {}
) {
  const { onSuccess, onError } = options;

  const mutation = useMutation<LoginResponse, LoginRequest>(
    async (credentials: LoginRequest) => {
      // API expects userName field, not username
      const requestBody = {
        userName: credentials.username || credentials.userName,
        password: credentials.password,
      };

      const response = await adminApiClient.post<LoginResponse>(
        '/api/admin/login',
        requestBody
      );

      // Store token if present in response
      if (response.token) {
        adminApiClient.setAuthToken(response.token);
        localStorage.setItem('authToken', response.token);
      }

      // Store user data if present
      if (response.userName || response.email) {
        localStorage.setItem('user', JSON.stringify({
          id: response.id,
          userName: response.userName,
          email: response.email,
          roles: response.roles || [],
          firstName: response.firstName,
          lastName: response.lastName,
        }));
      }

      return response;
    },
    {
      onSuccess: (data, variables) => {
        onSuccess?.(data, variables);
      },
      onError: (error, variables) => {
        onError?.(error, variables);
      },
    }
  );

  return mutation;
}

/**
 * Hook to handle logout
 */
export function useLogout(
  options: {
    onSuccess?: () => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const { onSuccess, onError } = options;
  const [loading, setLoading] = useState(false);

  const logout = useCallback(async () => {
    setLoading(true);
    try {
      // Call logout API endpoint
      try {
        await adminApiClient.post<ApiResponse>('/api/admin/logout');
      } catch (apiError) {
        // Continue with local cleanup even if API call fails
      }

      // Clear token from client
      adminApiClient.clearAuthToken();

      // Clear token from localStorage
      localStorage.removeItem('authToken');

      // Clear any other auth-related data
      localStorage.removeItem('user');

      onSuccess?.();
    } catch (error) {
      const err = error instanceof Error ? error : new Error('Logout failed');
      onError?.(err);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [onSuccess, onError]);

  return {
    logout,
    loading,
  };
}

/**
 * Hook to check authentication status
 */
export function useAuth() {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(() => {
    const token = localStorage.getItem('authToken');
    if (token) {
      adminApiClient.setAuthToken(token);
      return true;
    }
    return false;
  });

  const login = useCallback((token: string) => {
    adminApiClient.setAuthToken(token);
    localStorage.setItem('authToken', token);
    setIsAuthenticated(true);
  }, []);

  const logout = useCallback(() => {
    adminApiClient.clearAuthToken();
    localStorage.removeItem('authToken');
    localStorage.removeItem('user');
    setIsAuthenticated(false);
  }, []);

  return {
    isAuthenticated,
    login,
    logout,
    token: adminApiClient.getAuthToken(),
  };
}

/**
 * Hook to restore authentication from localStorage
 */
export function useRestoreAuth() {
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const restore = useCallback(() => {
    try {
      const token = localStorage.getItem('authToken');
      if (token) {
        adminApiClient.setAuthToken(token);
        setIsAuthenticated(true);
      }
    } catch (error) {
      setIsAuthenticated(false);
    } finally {
      setLoading(false);
    }
  }, []);

  // Auto-restore on mount
  useState(() => {
    restore();
  });

  return {
    loading,
    isAuthenticated,
    restore,
  };
}

