/**
 * Hook to handle change password
 */
export function useChangePassword(
  options: {
    onSuccess?: (data: ApiResponse, request: ChangePasswordRequest) => void;
    onError?: (error: Error, request: ChangePasswordRequest) => void;
  } = {}
) {
  const { onSuccess, onError } = options;

  const mutation = useMutation<ApiResponse, ChangePasswordRequest>(
    async (request: ChangePasswordRequest) => {
      // Get user ID from localStorage
      const userStr = localStorage.getItem('user');
      if (!userStr) {
        throw new Error('User not found');
      }
      const user = JSON.parse(userStr);
      const userId = user.id;

      // API endpoint: GET /api/admin/user_change_password/{id}?existingPassword=xxx&newPassword=yyy
      // Note: The API uses GET for this operation as per admin.json
      const response = await adminApiClient.get<ApiResponse>(
        `/api/admin/user_change_password/${userId}`,
        {
          existingPassword: request.currentPassword,
          newPassword: request.newPassword
        }
      );
      return response;
    },
    {
      onSuccess: (data, variables) => {
        onSuccess?.(data, variables);
      },
      onError: (error, variables) => {
        onError?.(error, variables);
      },
    }
  );

  return mutation;
}

/**
 * Hook for admin to reset another user's password
 * Endpoint: GET /api/admin/user_change_password/{userId}?existingPassword=xxx&newPassword=yyy
 */
export function useAdminResetUserPassword(
  options: {
    onSuccess?: (data: ApiResponse, variables: { userId: number | string; existingPassword: string; newPassword: string; confirmPassword: string }) => void;
    onError?: (error: Error, variables: { userId: number | string; existingPassword: string; newPassword: string; confirmPassword: string }) => void;
  } = {}
) {
  const { onSuccess, onError } = options;

  const mutation = useMutation<ApiResponse, { userId: number | string; existingPassword: string; newPassword: string; confirmPassword: string }>(
    async ({ userId, existingPassword, newPassword }) => {
      const userIdNum = typeof userId === "string" ? parseInt(userId, 10) : userId;

      // API endpoint: GET /api/admin/user_change_password/{userId}?existingPassword=xxx&newPassword=yyy
      // Using GET request with query parameters as per API specification
      return await adminApiClient.get<ApiResponse>(
        `/api/admin/user_change_password/${userIdNum}`,
        {
          existingPassword: existingPassword,
          newPassword: newPassword,
        }
      );
    },
    {
      onSuccess: (data, variables) => {
        onSuccess?.(data, variables);
      },
      onError: (error, variables) => {
        onError?.(error, variables);
      },
    }
  );

  return mutation;
}

/**
 * Hook to get current user profile
 */
export function useGetCurrentUser(
  options: {
    enabled?: boolean;
    onSuccess?: (data: AdminUserResponse) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const { enabled = true, onSuccess, onError } = options;

  const query = useQuery<AdminUserResponse>(
    async () => {
      const response = await adminApiClient.get<AdminUserResponse>('/api/admin/me');
      return response;
    },
    {
      enabled,
      onSuccess: (data) => {
        onSuccess?.(data);
      },
      onError: (error) => {
        onError?.(error);
      },
    }
  );

  return query;
}
