/**
 * Dashboard API Hooks
 * React hooks for dashboard data endpoints
 * Based on dashboard-controller from admin-docs.json
 */

import { useCallback } from 'react';
import { useFetch, buildUrl } from './useApi';
import { ADMIN_API_BASE_URL } from '../config';
import { getAuthToken } from './useAdminRefactored';
import type {
  DateRangeParams,
  TodayTripsResponse,
  TripsBetweenDatesResponse,
  TodayRevenueResponse,
  RevenueBetweenDatesResponse,
  TodayGMVResponse,
  GMVBetweenDatesResponse,
  OnDutyDriverListResponse,
  ActiveRidesResponse,
  LiveHeatmapResponse,
  RideHistoryItem,
} from './dashboardTypes';
import type { ApiResponse, PagedData } from './types';

const API_BASE_URL = ADMIN_API_BASE_URL;

/**
 * Get authentication headers
 */
function getAuthHeaders(): Record<string, string> {
  const token = getAuthToken();
  return token ? { Authorization: `Bearer ${token}` } : {};
}

// ============================================================================
// Trip Endpoints
// ============================================================================

/**
 * Hook to fetch today's trips data
 * GET /dashboard/trips/today
 */
export function useTodayTrips(
  options: {
    enabled?: boolean;
    onSuccess?: (data: TodayTripsResponse) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const url = buildUrl(API_BASE_URL, '/dashboard/trips/today');
  
  return useFetch<TodayTripsResponse>(url, {
    enabled: options.enabled !== false,
    headers: getAuthHeaders(),
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to fetch trips data between dates
 * GET /dashboard/trips/between-dates
 */
export function useTripsBetweenDates(
  params: DateRangeParams,
  options: {
    enabled?: boolean;
    onSuccess?: (data: TripsBetweenDatesResponse) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  const url = buildUrl(API_BASE_URL, '/dashboard/trips/between-dates', {
    from: params.from,
    to: params.to,
  });
  
  return useFetch<TripsBetweenDatesResponse>(url, {
    enabled: options.enabled !== false && !!params.from && !!params.to,
    headers: getAuthHeaders(),
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

