// ============================================================================
// Ride History Endpoint (for calculating revenue from individual rides)
// ============================================================================

/**
 * Hook to fetch ride history for today
 * GET /reports/ride-history
 * Used to calculate revenue from individual ride fares
 */
export function useTodayRideHistory(
  options: {
    enabled?: boolean;
    onSuccess?: (data: ApiResponse<PagedData<RideHistoryItem>>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Get today's date range (start and end of today) - recalculate on each call
  const getTodayDateRange = () => {
    const now = new Date();
    const startOfToday = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 0, 0, 0, 0);
    const endOfToday = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 23, 59, 59, 999);
    
    return {
      from: startOfToday.toISOString(),
      to: endOfToday.toISOString(),
    };
  };

  const dateRange = getTodayDateRange();
  const url = buildUrl(API_BASE_URL, '/reports/ride-history', {
    from: dateRange.from,
    to: dateRange.to,
    page: 0,
    size: 1000, // Get all rides for today
  });
  
  return useFetch<ApiResponse<PagedData<RideHistoryItem>>>(url, {
    enabled: options.enabled !== false,
    headers: getAuthHeaders(),
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}
