/**
 * Referral Campaign Management API Hooks
 * React hooks for managing referral campaign data
 */

import { useCallback } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { pricingApiClient } from './pricingApiClient';
import {
  ReferralCampaign,
  ReferralCampaignDTO,
  ReferralCampaignListQueryParams,
  PagedData,
  ReferralCampaignForm
} from './referralCampaignTypes';
import { ApiResponse } from './types';

/**
 * Hook to fetch a single referral campaign by ID
 * @param id - The referral campaign ID
 * @param options - Hook options
 */
export function useReferralCampaign(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: ReferralCampaign) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const response = await pricingApiClient.get<ApiResponse<ReferralCampaign>>(`/referral-campaign/${id}`);

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as ReferralCampaign;
  }, [id]);

  return useQuery<ReferralCampaign>({
    queryKey: ['referral-campaign', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to fetch paginated list of referral campaigns
 * @param params - Query parameters (page, size, sort, search)
 * @param options - Hook options
 */
export function useReferralCampaigns(
  params: ReferralCampaignListQueryParams = {},
  options: {
    enabled?: boolean;
    onSuccess?: (data: PagedData<ReferralCampaign>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};

    if (params.page !== undefined) {
      queryParams.page = params.page;
    }
    if (params.size !== undefined) {
      queryParams.size = params.size;
    }
    if (params.search) {
      queryParams.search = params.search;
    }
    if (params.isActive !== undefined) {
      queryParams.isActive = params.isActive;
    }
    if (params.referrerRewardType) {
      queryParams.referrerRewardType = params.referrerRewardType;
    }
    if (params.referredRewardType) {
      queryParams.referredRewardType = params.referredRewardType;
    }
    if (params.validityDays) {
      queryParams.validityDays = params.validityDays;
    }
    if (params.sort && params.sort.length > 0) {
      queryParams.sort = params.sort;
    }

    const response = await pricingApiClient.get<ApiResponse<PagedData<ReferralCampaign>>>(
      '/referral-campaign/list',
      queryParams
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as PagedData<ReferralCampaign>;
  }, [params.page, params.size, params.sort, params.search, params.isActive, params.referrerRewardType, params.referredRewardType, params.validityDays]);

  return useQuery<PagedData<ReferralCampaign>>({
    queryKey: ['referral-campaigns', params],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

