/**
 * Hook to create a new referral campaign
 * @param options - Hook options
 */
export function useCreateReferralCampaign(
  options: {
    onSuccess?: (data: ApiResponse<ReferralCampaign>, variables: ReferralCampaignForm) => void;
    onError?: (error: Error, variables: ReferralCampaignForm) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<ReferralCampaign>, Error, ReferralCampaignForm>({
    mutationFn: async (referralCampaignData: ReferralCampaignForm) => {
      const response = await pricingApiClient.post<ApiResponse<ReferralCampaign>>(
        '/referral-campaign',
        referralCampaignData
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all referral campaign queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['referral-campaigns'] });
      queryClient.invalidateQueries({ queryKey: ['all-referral-campaigns'] });
      queryClient.invalidateQueries({ queryKey: ['active-referral-campaigns'] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

/**
 * Hook to update an existing referral campaign
 * @param options - Hook options
 */
export function useUpdateReferralCampaign(
  options: {
    onSuccess?: (data: ApiResponse<ReferralCampaign>, variables: { id: number; data: ReferralCampaignForm }) => void;
    onError?: (error: Error, variables: { id: number; data: ReferralCampaignForm }) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<ReferralCampaign>, Error, { id: number; data: ReferralCampaignForm }>({
    mutationFn: async ({ id, data }) => {
      const response = await pricingApiClient.put<ApiResponse<ReferralCampaign>>(
        `/referral-campaign/${id}`,
        data
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all referral campaign queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['referral-campaigns'] });
      queryClient.invalidateQueries({ queryKey: ['all-referral-campaigns'] });
      queryClient.invalidateQueries({ queryKey: ['active-referral-campaigns'] });
      queryClient.invalidateQueries({ queryKey: ['referral-campaign', variables.id] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

