/**
 * Utility function to format reward display
 */
export function formatRewardDisplay(type: string, value: number): string {
  switch (type) {
    case 'DISCOUNT':
      return `${value}%`;
    case 'CASHBACK':
    case 'CREDIT':
      return new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: 2,
      }).format(value);
    case 'FREE_RIDE':
      return `${value} Free Ride${value > 1 ? 's' : ''}`;
    case 'POINTS':
      return `${value} Points`;
    default:
      return `${value} ${type}`;
  }
}

/**
 * Utility function to get reward type display name
 */
export function getRewardTypeDisplay(type: string): string {
  const types: Record<string, string> = {
    'DISCOUNT': 'Discount (%)',
    'CASHBACK': 'Cashback ($)',
    'CREDIT': 'Account Credit ($)',
    'FREE_RIDE': 'Free Rides',
    'POINTS': 'Loyalty Points'
  };
  return types[type] || type;
}