/**
 * Vehicle Category Management API Hooks
 * React hooks for managing vehicle category data
 */

import { useCallback } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { vehicleCategoryApiClient } from './vehicleCategoryApiClient';
import {
  VehicleCategory,
  VehicleCategoryDTO,
  VehicleCategoryListQueryParams,
  ApiResponse,
  PagedData
} from './vehicleCategoryTypes';

/**
 * Hook to fetch a single vehicle category by ID
 * @param id - Vehicle Category ID
 * @param options - Hook options
 */
export function useVehicleCategory(
  id: number,
  options: {
    enabled?: boolean;
    onSuccess?: (data: VehicleCategory) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const response = await vehicleCategoryApiClient.get<ApiResponse<VehicleCategory>>(`/vehicle-category/${id}`);

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as VehicleCategory;
  }, [id]);

  return useQuery<VehicleCategory>({
    queryKey: ['vehicle-category', id],
    queryFn,
    enabled: options.enabled !== false && !!id,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

/**
 * Hook to fetch paginated list of vehicle categories
 * @param params - Query parameters (page, size, sort, search)
 * @param options - Hook options
 */
export function useVehicleCategories(
  params: VehicleCategoryListQueryParams = {},
  options: {
    enabled?: boolean;
    onSuccess?: (data: PagedData<VehicleCategory>) => void;
    onError?: (error: Error) => void;
  } = {}
) {
  // Use useCallback to memoize the query function and prevent infinite re-renders
  const queryFn = useCallback(async () => {
    const queryParams: Record<string, any> = {};

    if (params.page !== undefined) {
      queryParams.page = params.page;
    }
    if (params.size !== undefined) {
      queryParams.size = params.size;
    }
    if (params.sort && params.sort.length > 0) {
      queryParams.sort = params.sort;
    }
    if (params.search) {
      queryParams.search = params.search;
    }
    if (params.isActive !== undefined) {
      queryParams.isActive = params.isActive;
    }

    // For GET request with pagination, we need to send pageable in query params
    const response = await vehicleCategoryApiClient.get<ApiResponse<PagedData<VehicleCategory>>>(
      '/vehicle-category/list',
      queryParams
    );

    // Handle Response wrapper
    if (response.data) {
      return response.data;
    }

    return response.data as PagedData<VehicleCategory>;
  }, [params.page, params.size, params.sort, params.search, params.isActive]);

  return useQuery<PagedData<VehicleCategory>>({
    queryKey: ['vehicle-categories', params],
    queryFn,
    enabled: options.enabled !== false,
    onSuccess: options.onSuccess,
    onError: options.onError,
  });
}

