/**
 * Hook to create a new vehicle category
 * @param options - Hook options
 */
export function useCreateVehicleCategory(
  options: {
    onSuccess?: (data: ApiResponse<VehicleCategory>, variables: VehicleCategoryDTO) => void;
    onError?: (error: Error, variables: VehicleCategoryDTO) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<VehicleCategory>, Error, VehicleCategoryDTO>({
    mutationFn: async (vehicleCategoryData: VehicleCategoryDTO) => {
      const response = await vehicleCategoryApiClient.post<ApiResponse<VehicleCategory>>(
        '/vehicle-category',
        vehicleCategoryData
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all vehicle category queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['all-vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories-paginated'] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

/**
 * Hook to update an existing vehicle category
 * @param options - Hook options
 */
export function useUpdateVehicleCategory(
  options: {
    onSuccess?: (
      data: ApiResponse<VehicleCategory>,
      variables: { id: number; data: VehicleCategoryDTO }
    ) => void;
    onError?: (error: Error, variables: { id: number; data: VehicleCategoryDTO }) => void;
  } = {}
) {
  const queryClient = useQueryClient();

  return useMutation<ApiResponse<VehicleCategory>, Error, { id: number; data: VehicleCategoryDTO }>({
    mutationFn: async ({ id, data }: { id: number; data: VehicleCategoryDTO }) => {
      const response = await vehicleCategoryApiClient.put<ApiResponse<VehicleCategory>>(
        `/vehicle-category/${id}`,
        data
      );
      return response;
    },
    onSuccess: (data, variables) => {
      // Invalidate all vehicle category queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ['vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['all-vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories-paginated'] });
      queryClient.invalidateQueries({ queryKey: ['vehicle-category', variables.id] });
      
      if (options.onSuccess) {
        options.onSuccess(data, variables);
      }
    },
    onError: options.onError,
  });
}

