      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories'] });
      queryClient.invalidateQueries({ queryKey: ['active-vehicle-categories-paginated'] });
      queryClient.invalidateQueries({ queryKey: ['vehicle-category', id] });
      
      if (options.onSuccess) {
        options.onSuccess(data, id);
      }
