export const buildCityPayload = async (
  formData: CityFormData,
  id?: string | number,
  existingData?: CityMasterDTO
): Promise<CityMasterDTO> => {
  const clientIp = await getClientIp();
  const isEditing = id !== undefined && existingData;
  
  return {
    cityId: id !== undefined ? Number(id) : undefined,
    cityName: formData.name,
    status: mapStatusToApi(formData.status),
    state:
      formData.stateId && formData.stateId !== 'no-state'
        ? {
            stateId: Number(formData.stateId),
            // Include system IP fields for nested state if available
            ...(existingData?.state && {
              createdSystemIp: existingData.state.createdSystemIp || clientIp,
              modifiedSystemIp: clientIp,
            }),
            country: formData.countryId
              ? {
                  countryId: Number(formData.countryId),
                  // Include system IP fields for nested country if available
                  ...(existingData?.state?.country && {
                    createdSystemIp: existingData.state.country.createdSystemIp || clientIp,
                    modifiedSystemIp: clientIp,
                  }),
                }
              : undefined,
          }
        : undefined,
    remarks: formData.metadata?.remarks,
    createdSystemIp: isEditing ? (existingData.createdSystemIp || clientIp) : clientIp,
    modifiedSystemIp: clientIp,
    // Preserve existing values when editing
    ...(existingData && {
      createdBy: existingData.createdBy,
      createdDate: existingData.createdDate,
      modifiedBy: existingData.modifiedBy,
      modifiedDate: existingData.modifiedDate,
      isDeletedValue: existingData.isDeletedValue,
    }),
  };
};

export const mapCityToPayload = (city: City): CityMasterDTO => ({
  cityId: city.id ? Number(city.id) : undefined,
  cityName: city.name,
  status: mapStatusToApi(city.status),
  state:
    city.stateId && city.stateId !== 'no-state'
      ? {
          stateId: Number(city.stateId),
          country: city.countryId
            ? {
                countryId: Number(city.countryId),
              }
            : undefined,
        }
      : undefined,
  remarks: city.metadata?.remarks,
});
